:root {
  --limegreen: hsl(163, 72%, 41%);
  --brightred: hsl(356, 69%, 56%);
  --facebook: hsl(208, 92%, 53%);
  --twitter: hsl(203, 89%, 53%);
  --instagram-start: hsl(37, 97%, 70%);
  --instagram-middle: hsl(5, 77%, 71%);
  --instagram-end: hsl(329, 70%, 58%);
  --youtube: hsl(348, 97%, 39%);
  --toggle-bg-light: hsl(230, 22%, 74%);
  --toggle-bg-start: hsl(210, 78%, 56%);
  --toggle-bg-end: hsl(146, 68%, 55%);
  --toggle-light: hsl(230, 19%, 60%);
  --toggle-button-light: hsl(228, 46%, 96%);
  --dark-bg: hsl(230, 17%, 14%);
  --dark-top-bg: hsl(232, 19%, 15%);
  --dark-card: hsl(228, 28%, 20%);
  --dark-card-hover: hsl(228, 25%, 27%);
  --dark-text1: hsl(228, 34%, 66%);
  --dark-text2: hsl(0, 0%, 100%);
  --light-bg: hsl(0, 0%, 100%);
  --light-top-bg: hsl(225, 100%, 98%);
  --light-card: hsl(227, 47%, 96%);
  --light-card-hover: hsl(228, 33%, 91%);
  --light-text1: hsl(230, 12%, 44%);
  --light-text2: hsl(230, 17%, 14%);
}

html {
  font-size: 100%;
  box-sizing: border-box;
}

*, *::after, *::before {
  box-sizing: inherit;
}

body {
  font-weight: bolder;
  background: linear-gradient(to bottom, var(--light-top-bg) 0px, var(--light-top-bg) 15%, var(--light-top-bg) 200px, var(--light-bg) 50px, var(--light-bg) 100%);
  margin: 0 10%;
  padding: 0;
  font-family: var(--font-inter);
}

.top-bar {
  margin-bottom: 35px;
}

h1, h2, h3 {
  margin: 0px;
  margin-top: 10px;
}

a, a:visited, a:active {
  text-decoration: none;
}

:root {
  --font-inter:"Inter",sans-serif;
}

.social-card-container {
  display: flex;
  gap: 30px;
  flex-wrap: wrap;
}

.card {
  align-items: center;
  flex: 1 1 calc(25% - 30px);
  text-align: center;
  display: grid;
  background-color: var(--light-card);
  border-top: 4px solid black;
  border-radius: 4%;
}

#facebook-card {
  border-color: var(--facebook);
}

#twitter-card {
  border-color: var(--twitter);
}

#instagram-card {
  border-color: var(--instagram-start);
}

#instagram-card:hover {
  border-color: var(--instagram-end);
}

#youtube-card {
  border-color: var(--youtube);
}

.card:hover {
  background-color: var(--light-card-hover);
}

.logo-one {
  white-space: nowrap;
}
.logo-one img, .logo-one p {
  display: inline-block;
}
.logo-one p {
  color: var(--light-text2);
}

.numberOf {
  margin: 0;
  padding: 0;
  color: hsl(230, 17%, 14%);
  font-size: 450%;
}

.followers {
  color: var(--light-text1);
  letter-spacing: 2px;
}

.updates-total {
  color: var(--limegreen);
}

.social-card-container {
  margin-bottom: 40px;
}

#overview-today {
  color: var(--light-text1);
}

.overview-card-container {
  display: flex;
  gap: 30px;
  flex-wrap: wrap;
  margin-top: 30px;
}

.overview-card-container .card {
  border: none;
  display: grid;
  grid-template-columns: repeat(2, 1fr); /* Two columns */
  grid-template-rows: repeat(2, 1fr); /* Two rows */
}
.overview-card-container .card img {
  margin-left: 65px;
  height: auto;
  width: 35px;
}
.overview-card-container .card p {
  color: var(--light-text1);
}
.overview-card-container .card .updates-overview {
  color: var(--limegreen);
}

h3 {
  margin: 0;
  padding: 0;
  color: hsl(230, 17%, 14%);
  font-size: 250%;
}

#toggle-text {
  color: var(--light-text1);
}

.switch {
  margin-left: 20px;
  position: relative;
  display: inline-block;
  width: 60px;
  height: 34px;
}

/* Hide default HTML checkbox */
.switch input {
  opacity: 0;
  width: 0;
  height: 0;
}

/* The slider */
.slider {
  position: absolute;
  cursor: pointer;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background-color: var(--toggle-bg-light);
  -webkit-transition: 0.4s;
  transition: 0.4s;
}

.slider:before {
  position: absolute;
  content: "";
  height: 26px;
  width: 26px;
  left: 4px;
  bottom: 4px;
  background-color: white;
  -webkit-transition: 0.4s;
  transition: 0.4s;
}

input:checked + .slider {
  background: linear-gradient(to right, var(--toggle-bg-start), var(--toggle-bg-end));
}

input:focus + .slider {
  box-shadow: 0 0 1px #2196F3;
}

input:checked + .slider:before {
  -webkit-transform: translateX(26px);
  -ms-transform: translateX(26px);
  transform: translateX(26px);
}

/* Rounded sliders */
.slider.round {
  border-radius: 34px;
}

.slider.round:before {
  border-radius: 50%;
}

.toggle {
  text-align: right;
}

#total-fol {
  margin: 0;
  color: var(--light-text1);
}

#overview-today {
  margin: 1cm 0;
}

[data-theme=dark] body {
  background: linear-gradient(to bottom, var(--dark-top-bg) 0px, var(--dark-top-bg) 15%, var(--dark-top-bg) 200px, var(--dark-bg) 50px, var(--dark-bg) 100%);
}
[data-theme=dark] .card {
  background-color: var(--dark-card);
}
[data-theme=dark] .card:hover {
  background-color: var(--dark-card-hover);
}
[data-theme=dark] h1 {
  color: var(--dark-text2);
}
[data-theme=dark] .numberOf {
  color: var(--dark-text2);
}
[data-theme=dark] .overview-today {
  color: var(--dark-text2);
}
[data-theme=dark] .toggle-text {
  color: var(--dark-text2);
}
[data-theme=dark] .logo-one p {
  color: var(--dark-text1);
}
[data-theme=dark] .overview-today .card p {
  color: var(--dark-text1);
}